<template>
  <div id="app">
    <div class="container">
      <div class="pb-2 mb-3 border-bottom">
        <h4 class="text-left">Data Integration Demo</h4>
      </div>
      <div class="row">
        <div v-for="source in dataSource" :key="source.name" class="col-12 col-md-6 col-lg-4 my-2">
          <DataSource :data="source" :modalName="source.name"/>
        </div>
      </div>
      <div class="d-flex flex-column flex-md-row mt-5">
        <div class="d-flex flex-column align-items-start">
          <h4 class="mb-0">Integrate Data</h4>
          <small class="text-muted text-left">Click the button below to integrate data from the selected data sources</small>
          <button style="width: 170px;" @click="integrateData" :disabled="isIntegratingData" class="btn btn-primary btn-lg my-3">
            <span v-if="!isIntegratingData">Integrate Data</span>
            <div v-else class="spinner">
              <div class="bounce1"></div>
              <div class="bounce2"></div>
              <div class="bounce3"></div>
            </div>
          </button>
        </div>
       <div v-if="doneIntegrating" class="ml-0 ml-md-5 mt-3" style="flex-basis: 60%">
          <DataSource :data="integratedData" :modalName="'integrated'"/>
       </div>
      </div>
    </div>
    <modal name="integrating">
      <div class="w-100 h-100 d-flex flex-column justify-content-center align-items-center">
        <div class="sk-chase">
          <div class="sk-chase-dot"></div>
          <div class="sk-chase-dot"></div>
          <div class="sk-chase-dot"></div>
          <div class="sk-chase-dot"></div>
          <div class="sk-chase-dot"></div>
          <div class="sk-chase-dot"></div>
        </div>
        <h5 class="mt-3">{{currentProcess}}</h5>
      </div>
    </modal>
  </div>
</template>

<script>
import DataSource from './components/DataSource.vue'
import dataSource from './data/data-sources';

export default {
  name: 'App',
  components: {
    DataSource
  },
  data () {
    return {
      dataSource: dataSource,
      isIntegratingData: false,
      dataIntegratingProcesses: ['Normalizing Data..', 'Removing Duplicates...', 'Integrity Violation Checks...'],
      currentProcessIndex: 0,
      currentProcess: '',
      integratedData: { name: "Integrated Data" },
      doneIntegrating: false
    }
  },
  methods: {
    integrateData(){
      this.isIntegratingData = true;
      this.$modal.show("integrating");
      this.currentProcess = this.dataIntegratingProcesses[this.currentProcessIndex];
      let interval = setInterval(() => {
        if(this.currentProcessIndex != 2){
          this.currentProcess = this.dataIntegratingProcesses[++this.currentProcessIndex];
        } else {
          clearInterval(interval);
          this.$modal.hide("integrating");
          this.doneIntegrating = true;
          this.isIntegratingData = false;
        }
      }, 3000);
    }
  }
}
</script>

